import React, { useState, useEffect, memo } from 'react';
import Editor from '@monaco-editor/react';
import axios from 'axios';
import Avatar from 'react-avatar';
import Modal from './Modal';
import MarkdownPreview from '@uiw/react-markdown-preview';

const BASE_URL = "https://6011m780-5000.inc1.devtunnels.ms/";

const Main = memo(({ socket, questionId, players, player, roomCode }) => {
  const [code, setCode] = useState('');
  const [output, setOutput] = useState('');
  const [customInput, setCustomInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [subLoading, setSubLoading] = useState(false);
  const [question, setQuestion] = useState({});
  const [testResults, setTestResults] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [winner, setWinner] = useState("");
  const [t, setT] = useState(0);

  const handleChange = (newValue) => {
    setCode(newValue);
  };

  useEffect(() => {
    let tim = setInterval(() => {
      setT(p => p + 1);
    }, 1000);
    return () => clearInterval(tim);
  }, []);

  useEffect(() => {
    socket.on('wonGame', ({ winner }) => {
      setWinner(winner);
      setIsModalOpen(true);
    });

    return () => socket.off("wonGame");
  }, [questionId]);

  useEffect(() => {
    axios(BASE_URL + "question/" + questionId).then(res => {
      setQuestion(res.data.question);
    });
  }, [questionId]);

  const handleCustomInputChange = (e) => {
    setCustomInput(e.target.value);
  };

  const executeCode = async (code, input) => {
    setLoading(true);
    try {
      const response = await axios.post(BASE_URL + "checkAgainstInput/" + questionId, {
        code: code,
        input: customInput,
        userId: "Siddhartha Bajpai"
      });
      return response.data.output;
    } catch (error) {
      console.error("Error executing code:", error);
      return "";
    } finally {
      setLoading(false);
    }
  };

  const handleRun = async () => {
    const result = await executeCode(code, customInput);
    setOutput(result);
  };

  const handleSubmit = async () => {
    setSubLoading(true);
    try {
      const response = await axios.post(BASE_URL + "submit/" + questionId, {
        code: code,
        input: customInput,
        userId: "Siddhartha Bajpai"
      });

      setTestResults(response.data.output);

      if (response.data.status === "accepted") {
        socket.emit('accepted', { player: player, roomCode: roomCode });
      }
    } catch (error) {
      console.error("Error submitting:", error);
    } finally {
      setSubLoading(false);
    }
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const handleEditorDidMount = (editor) => {
    editor.onDidPaste((event) => {
      event.preventDefault();
      alert("Pasting code is not allowed!");
    });
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="mb-6 grid grid-cols-2 gap-4">
        <div className="col-span-1 flex flex-col justify-between">
          <div className="mb-4">
            <h1 className="text-2xl font-bold mb-2">{question.questionTitle}</h1>
            <MarkdownPreview source={question.questionStatement} style={{ padding: 16 }} />
          </div>
          <div>
            <textarea
              type="text"
              className="w-full border border-gray-300 rounded-md p-2 mb-4"
              value={customInput}
              onChange={handleCustomInputChange}
              placeholder="Enter custom input..."
            />
            <div>
              <button
                className={`bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded-md mr-4 ${loading ? 'opacity-50 cursor-not-allowed' : ''}`}
                onClick={handleRun}
                disabled={loading}
              >
                {loading ? 'Running...' : 'Run'}
              </button>
              <button
                className={`bg-green-500 hover:bg-green-600 text-white px-4 py-2 rounded-md ${subLoading ? 'opacity-50 cursor-not-allowed' : ''}`}
                onClick={handleSubmit}
                disabled={subLoading}
              >
                {loading ? 'Submitting...' : 'Submit'}
              </button>
            </div>
          </div>
        </div>
        <div className="col-span-1">
          <div className='flex justify-between align-center'>
            <div>
              {players.map((player, index) => (
                <Avatar key={index} name={player} size='40' round="100px" title={player} />
              ))}
            </div>
            <h1 className='text-[2rem] font-bold'>{Math.floor(t / 60) < 10 ? "0" : null}{Math.floor(t / 60)}:{t % 60 < 10 ? "0" : null}{t % 60}</h1>
          </div>
          <Editor
            height="400px"
            language="python"
            theme="vs-dark"
            value={code}
            onChange={handleChange}
            editorDidMount={handleEditorDidMount}
            options={{
              selectOnLineNumbers: true,
              fontSize: 16,
              automaticLayout: true,
            }}
          />
        </div>
      </div>
      {testResults.length > 0 && (
        <div className="bg-gray-200 p-4 rounded-md">
          <h2 className="text-xl font-bold mb-2">Submission Results</h2>
          <div className="grid grid-cols-5 gap-4">
            {testResults.map((test, index) => (
              <div key={index} className={`text-lg ${test.status === 'test passed' ? 'text-green-500' : 'text-red-500'}`}>
                {test.status === 'test passed' ? `Test ${index + 1}: Passed` : `Test ${index + 1}: Failed`}
              </div>
            ))}
          </div>
        </div>
      )}
      <div>
        <h2 className="text-xl font-bold mb-2">Output</h2>
        <pre className="bg-gray-100 p-4 rounded-md">{output}</pre>
      </div>
      {isModalOpen && (
        <Modal onClose={closeModal}>
          <div className="text-center">
            <h2 className="text-2xl font-bold mb-4">Successfully Solved by {winner}</h2>
            <p className="text-lg mb-4">Congratulations!</p>
            <p className="text-lg mb-4">If you lost, no problem 😥 but if you won, you deserve a 👑</p>
          </div>
        </Modal>
      )}
    </div>
  );
});

export default Main;
