import Main from './Main';
import React, { useState ,useEffect} from 'react';
import io from 'socket.io-client';
import './App.css';
import Avatar from 'react-avatar';

const socket = io('https://6011m780-5000.inc1.devtunnels.ms'); 

function App() {
  const [isAdmin , setIsAdmin] = useState(false);
  const [roomCode, setRoomCode] = useState('');
  const [gameOn,setGameOn] = useState(false);
  const [playerName, setPlayerName] = useState('');
  const [message, setMessage] = useState('');
  const [playersInRoom, setPlayersInRoom] = useState([]);
  const [questionId, setQuestionId] = useState('');
  const [showOverPanel,setShowOverPanel] = useState(false);
  const isNameFieldEmpty = playerName.trim() === '';
 

  useEffect(()=>{
console.log(gameOn,questionId);
  },[gameOn,questionId])

  useEffect(()=>{
    
    socket.on('playerJoined', ({ players }) => {
      console.log(players)
      setPlayersInRoom(players);
      console.log("victom")
    });
    socket.on('roomNotFound', () => {
      setMessage('Room not found');
      console.log("victom")

    });


    socket.on('roomCreated', ({ roomCode ,players}) => {
      setRoomCode(roomCode);
      // setMessage(`Room created with code: ${roomCode}`);
    setShowOverPanel(true)
    setPlayersInRoom(players)
    console.log("victom")

     
  

     
    });
    socket.on('gameStarted', ({ questionId }) => {
      console.log("game started")
      console.log("question changes");
      console.log("victom")

      setQuestionId(questionId);
      setMessage(`Game started with question ID: ${questionId}`);
      setGameOn(true)
    });



    return ()=>{
      socket.off('playerJoined');
      socket.off('roomNotFound');
      socket.off('roomCreated')
      socket.off('gameStarted')

    }
  },[questionId])
  const handleJoinRoom = () => {
    
    console.log("handle join room")
    socket.emit('joinRoom', { roomCode, playerName });
   
   
    setShowOverPanel(true)
  };

  const handleCreateRoom = () => {
    setIsAdmin(true);
    socket.emit('createRoom',{playerName});
    
  };


  
  
  const handleStartGame = () => {
    console.log(questionId)
    if(questionId !="")
    return ;
    socket.emit('startGame', { roomCode, questionId });
    socket.on('gameStarted', ({ questionId }) => {
      console.log("game started")
      console.log("question changes");
      setQuestionId(questionId);
      setMessage(`Game started with question ID: ${questionId}`);
      setGameOn(true)
    });
  };
  if(gameOn)
  return <Main roomCode = {roomCode} socket = {socket} questionId = {questionId} player = {playerName} players = {playersInRoom}></Main>
else
  return (
    <div
      className="h-screen flex justify-center items-center"
      style={{ backgroundColor: '#6B46C1' }}
    >

{showOverPanel?(<div>

  <div class="absolute top-0 left-0 w-full bg-gray-800 bg-opacity-50 h-screen flex items-center justify-center">
  <div class="absolute inset-0 backdrop-filter backdrop-blur-lg"></div>
  
  <div class="relative p-6 bg-white rounded-lg shadow-xl">
    <h2 class="text-2xl font-bold mb-4">Room Code {roomCode}</h2>
    <p class="text-gray-700">Waiting For Players ...</p>
    <p class="text-gray-700">Players in room</p>

  {playersInRoom.map((player, index) => (<>
            <Avatar name={player} size='40' round = "100px" title={player} /></>
          ))}
         {isAdmin && <button  
        className="w-full  bg-green-500 hover:bg-green-600 text-white px-4 py-2 rounded-md" onClick={handleStartGame}
      >Start Game</button>}
  </div>
  
</div>


</div>):null}

      <div className="bg-white p-8 rounded-md shadow-lg">

      <input
          type="text"
          className="w-full mb-10 border border-gray-300 rounded-md p-2"
          placeholder="Enter Your Name"
          value={playerName}
          onChange={e=>setPlayerName(e.target.value)}
        />  
        
      <button  disabled={isNameFieldEmpty}
        className="w-full bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded-md" onClick={handleCreateRoom}
      >Create Room</button>
      <div class="flex my-1 items-center justify-center">
  <div class="border-t border-gray-300 w-full"></div>
  <div class="mx-4 text-gray-500 uppercase text-xs tracking-wider">OR</div>
  <div class="border-t border-gray-300 w-full"></div>
</div>
        <input
          type="text"
          className="w-full mb-2 border border-gray-300 rounded-md p-2"
          placeholder="Enter room code"
          value= {roomCode}
          onChange = {e=>setRoomCode(e.target.value)}
        />  
        <div className='flex flex-row'><button  disabled={isNameFieldEmpty}
        className="w-full  bg-green-500 hover:bg-green-600 text-white px-4 py-2 rounded-md" onClick={handleJoinRoom}
      >Join</button></div>
      </div>
   </div>
      
  );
}

export default App;
